<template>
  <div class="user-profile">
    <van-nav-bar class="page-nav-bar" title="" left-arrow @click-left="$router.back()" />
    <div>
      <van-cell title="" is-link center class="photo-cell" @click="$refs.file.click()">
        <van-image class="avatar" fit="cover" round :src="user.photo" />

        <!--  -->
        <input type="file" hidden ref="file" @change="onFileChange" />

      </van-cell>
      <van-cell title="" :value="user.name" is-link @click="isUpdateNameShow = true" />
      <van-cell title="" :value="user.gender === 0 ? '' : ''" is-link @click="isUpdateGenderShow = true" />
      <van-cell title="" :value="user.birthday" is-link @click="isUpdateBirthdayShow = true" />
    </div>
    <!-- + -->
    <van-popup v-model="isUpdateNameShow" style="height: 90%;" position="bottom">
      <NickName v-if="isUpdateNameShow" v-model="user.name" @close="isUpdateNameShow = false"></NickName>
    </van-popup>
    <!-- / -->

    <!--  -->
    <van-popup v-model="isUpdateGenderShow" position="bottom">
      <Gender v-if="isUpdateGenderShow" v-model="user.gender" @close="isUpdateGenderShow = false" />
    </van-popup>

    <!--  -->
    <van-popup v-model="isUpdateBirthdayShow" style="height: 40%;" position="bottom">
      <Birthday v-if="isUpdateBirthdayShow" v-model="user.birthday" @close="isUpdateBirthdayShow = false" />
    </van-popup>

    <!--  -->
    <van-popup v-model="isUpdatePhotoShow" style="height: 100%" position="bottom">
      <UserPhoto v-if="isUpdatePhotoShow" :img="img" @close="isUpdatePhotoShow = false"
        @update-photo="user.photo = $event"></UserPhoto>
    </van-popup>
    <!--  -->
  </div>
</template>

<script>
import { getUserProfileAPI } from '@/api/index'
import NickName from '@/components/User/NickName.vue'
import Gender from '@/components/User/Gender.vue'
import Birthday from '@/components/User/Birthday.vue'
import UserPhoto from '@/components/User/Photo.vue'
export default {
  components: {
    NickName,
    Gender,
    Birthday,
    UserPhoto
  },
  data () {
    return {
      user: {}, // 
      isUpdateNameShow: false, // 
      isUpdateGenderShow: false,
      isUpdateBirthdayShow: false,
      isUpdatePhotoShow: false,
      img: ''

    }
  },
  created () {
    // 
    this.loadProfile()
  },
  methods: {
    async loadProfile () {
      try {
        const { data: res } = await getUserProfileAPI()
        this.user = res.data
      } catch (err) {
        this.$toast('')
      }
    },
    onFileChange () {
      // 
      const file = this.$refs.file.files[0]
      //  blob 
      this.img = window.URL.createObjectURL(file)
      //  value 
      this.$refs.file.value = ''
      // 
      this.isUpdatePhotoShow = true
    }
  }
}
</script>

<style scoped lang='less'>
.user-profile {
  .avatar {
    width: 60px;
    height: 60px;
  }

  .photo-cell {
    .van-cell__value {
      display: flex;
      flex-direction: row-reverse;
    }
  }

  .van-popup {
    background: #f5f7f9;
  }

}
</style>
