<template>
  <div class="updata-photo">
    <img :src="img" ref="img" />
    <div class="toolbar">
      <span class="cancel" @click="$emit('close')"></span>
      <span class="confirm" @click="onConfirm"></span>
    </div>
  </div>
</template>

<script>
import { updateUserPhotoAPI } from '@/api/index'
import Cropper from 'cropperjs'
export default {
  props: {
    img: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      cropper: null
    }
  },
  mounted () {
    // 
    const image = this.$refs.img
    this.cropper = new Cropper(image, {
      viewMode: 1, // 0 1,2,3
      dragMode: 'move', //  move: 
      aspectRatio: 1, // , 1:11
      autoCropArea: 1, // 
      cropBoxMovable: false, // 
      cropBoxResizable: false, // 
      background: false, // 
      movable: true, // true
      crop (event) { // 
        console.log(event.detail)
        // console.log(event.detail.y)
        // console.log(event.detail.width)
        // console.log(event.detail.height)
        // console.log(event.detail.rotate)
        // console.log(event.detail.scaleX)
        // console.log(event.detail.scaleY)
      }
    })
  },
  methods: {
    onConfirm () {
      //  getData 
      // console.log(this.cropper.getData())

      //  getCroppedCanvas 
      this.cropper.getCroppedCanvas().toBlob(blob => {
        this.updateUserPhoto(blob)
      })
    },
    // 
    async updateUserPhoto (blob) {
      this.$toast.loading({
        message: '...',
        forbidClick: true, // 
        duration: 0 // 
      })
      try {
        //  Content-Type  multipart/form-data
        //  FormData 
        // formData
        const formData = new FormData()
        formData.append('photo', blob)
        const { data: res } = await updateUserPhotoAPI(formData)
        // 
        this.$emit('close')
        // 
        this.$emit('update-photo', res.data.photo)

        // 
        this.$toast.success('')
      } catch (error) {
        this.$toast.fail('')
      }
    }
  }
}
</script>

<style scoped lang='less'>
@import 'cropperjs/dist/cropper.css';

.updata-photo {
  background-color: #000;
  height: 100%;

  .toolbar {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: space-between;

    .cancel,
    .confirm {
      width: 90px;
      height: 90px;
      font-size: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #fff;
    }
  }
}

.img {
  display: block;
  max-width: 100%;
}
</style>
